body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Mont';
  font-weight: 400;
  src: local('Mont'), url(./fonts/Mont-Regular.otf) format('opentype');
}

@font-face {
  font-family: 'Mont';
  font-weight: 700;
  src: local('Mont'), url(./fonts//Mont-SemiBold.otf) format('opentype');
}

@font-face {
  font-family: 'Mont';
  font-weight: 800;
  src: local('Mont'), url(./fonts/Mont-Bold.otf) format('opentype');
}
